<template>
  <div :class="containerClass" data-scrollselectors=".p-datatable-wrapper">
    <slot></slot>
    <div class="p-datatable-loading-overlay p-component-overlay" v-if="loading">
      <i :class="loadingIconClass"></i>
    </div>
    <div class="p-datatable-header" v-if="$scopedSlots.header">
      <slot name="header"></slot>
    </div>
    <DTPaginator
      v-if="paginatorTop"
      ref="paginatorTop"
      :rows="d_rows"
      :first="lazy ? 0 : d_first"
      :totalRecords="totalRecordsLength"
      :pageLinkSize="pageLinkSize"
      :template="paginatorTemplate"
      :rowsPerPageOptions="rowsPerPageOptions"
      :currentPageReportTemplate="currentPageReportTemplate"
      class="p-paginator-top"
      @page="onPage($event)"
      :alwaysShow="alwaysShowPaginator">
      <template #start v-if="$scopedSlots.paginatorstart">
        <slot name="paginatorstart"></slot>
      </template>
      <template #end v-if="$scopedSlots.paginatorend">
        <slot name="paginatorend"></slot>
      </template>
    </DTPaginator>
    <div class="p-datatable-wrapper" :style="{ maxHeight: scrollHeight }">
      <table ref="table" role="table" :class="[tableClass, 'p-datatable-table']" :style="tableStyle">
        <DTTableHeader
          :columnGroup="headerColumnGroup"
          :columns="columns"
          :rowGroupMode="rowGroupMode"
          :groupRowsBy="groupRowsBy"
          :groupRowSortField="groupRowSortField"
          :resizableColumns="resizableColumns"
          :allRowsSelected="allRowsSelected"
          :empty="empty"
          :sortMode="sortMode"
          :sortField="d_sortField"
          :sortOrder="d_sortOrder"
          :multiSortMeta="d_multiSortMeta"
          :filters="d_filters"
          :filtersStore="filters"
          :filterDisplay="filterDisplay"
          @column-click="onColumnHeaderClick($event)"
          @column-mousedown="onColumnHeaderMouseDown($event)"
          @filter-change="onFilterChange"
          @filter-apply="onFilterApply"
          @column-dragstart="onColumnHeaderDragStart($event)"
          @column-dragover="onColumnHeaderDragOver($event)"
          @column-dragleave="onColumnHeaderDragLeave($event)"
          @column-drop="onColumnHeaderDrop($event)"
          @column-resizestart="onColumnResizeStart($event)"
          @checkbox-change="toggleRowsWithCheckbox($event)" />
        <DTTableBody
          v-if="frozenValue"
          :value="frozenValue"
          :frozenRow="true"
          class="p-datatable-frozen-tbody"
          :columns="columns"
          :dataKey="dataKey"
          :selection="selection"
          :selectionKeys="d_selectionKeys"
          :selectionMode="selectionMode"
          :contextMenu="contextMenu"
          :contextMenuSelection="contextMenuSelection"
          :rowGroupMode="rowGroupMode"
          :groupRowsBy="groupRowsBy"
          :expandableRowGroups="expandableRowGroups"
          :rowClass="rowClass"
          :rowStyle="rowStyle"
          :editMode="editMode"
          :compareSelectionBy="compareSelectionBy"
          :scrollable="scrollable"
          :expandedRowIcon="expandedRowIcon"
          :collapsedRowIcon="collapsedRowIcon"
          :expandedRows="expandedRows"
          :expandedRowKeys="d_expandedRowKeys"
          :expandedRowGroups="expandedRowGroups"
          :editingRows="editingRows"
          :editingRowKeys="d_editingRowKeys"
          :templates="$scopedSlots"
          :loading="loading"
          :responsiveLayout="responsiveLayout"
          @rowgroup-toggle="toggleRowGroup"
          @row-click="onRowClick($event)"
          @row-dblclick="onRowDblClick($event)"
          @row-rightclick="onRowRightClick($event)"
          @row-touchend="onRowTouchEnd"
          @row-keydown="onRowKeyDown"
          @row-mousedown="onRowMouseDown"
          @row-dragstart="onRowDragStart($event)"
          @row-dragover="onRowDragOver($event)"
          @row-dragleave="onRowDragLeave($event)"
          @row-dragend="onRowDragEnd($event)"
          @row-drop="onRowDrop($event)"
          @row-toggle="toggleRow($event)"
          @radio-change="toggleRowWithRadio($event)"
          @checkbox-change="toggleRowWithCheckbox($event)"
          @cell-edit-init="onCellEditInit($event)"
          @cell-edit-complete="onCellEditComplete($event)"
          @cell-edit-cancel="onCellEditCancel($event)"
          @row-edit-init="onRowEditInit($event)"
          @row-edit-save="onRowEditSave($event)"
          @row-edit-cancel="onRowEditCancel($event)"
          :editingMeta="d_editingMeta"
          @editing-meta-change="onEditingMetaChange" />
        <DTTableBody
          :value="dataToRender"
          :columns="columns"
          :empty="empty"
          :dataKey="dataKey"
          :selection="selection"
          :selectionKeys="d_selectionKeys"
          :selectionMode="selectionMode"
          :contextMenu="contextMenu"
          :contextMenuSelection="contextMenuSelection"
          :rowGroupMode="rowGroupMode"
          :groupRowsBy="groupRowsBy"
          :expandableRowGroups="expandableRowGroups"
          :rowClass="rowClass"
          :rowStyle="rowStyle"
          :editMode="editMode"
          :compareSelectionBy="compareSelectionBy"
          :scrollable="scrollable"
          :expandedRowIcon="expandedRowIcon"
          :collapsedRowIcon="collapsedRowIcon"
          :expandedRows="expandedRows"
          :expandedRowKeys="d_expandedRowKeys"
          :expandedRowGroups="expandedRowGroups"
          :editingRows="editingRows"
          :editingRowKeys="d_editingRowKeys"
          :templates="$scopedSlots"
          :loading="loading"
          :responsiveLayout="responsiveLayout"
          @rowgroup-toggle="toggleRowGroup"
          @row-click="onRowClick($event)"
          @row-dblclick="onRowDblClick($event)"
          @row-rightclick="onRowRightClick($event)"
          @row-touchend="onRowTouchEnd"
          @row-keydown="onRowKeyDown"
          @row-mousedown="onRowMouseDown"
          @row-dragstart="onRowDragStart($event)"
          @row-dragover="onRowDragOver($event)"
          @row-dragleave="onRowDragLeave($event)"
          @row-dragend="onRowDragEnd($event)"
          @row-drop="onRowDrop($event)"
          @row-toggle="toggleRow($event)"
          @radio-change="toggleRowWithRadio($event)"
          @checkbox-change="toggleRowWithCheckbox($event)"
          @cell-edit-init="onCellEditInit($event)"
          @cell-edit-complete="onCellEditComplete($event)"
          @cell-edit-cancel="onCellEditCancel($event)"
          @row-edit-init="onRowEditInit($event)"
          @row-edit-save="onRowEditSave($event)"
          @row-edit-cancel="onRowEditCancel($event)"
          :editingMeta="d_editingMeta"
          @editing-meta-change="onEditingMetaChange" />
        <DTTableFooter :columnGroup="footerColumnGroup" :columns="columns" />
      </table>
    </div>
    <DTPaginator
      v-if="paginatorBottom"
      ref="paginatorBottom"
      :rows="d_rows"
      :first="lazy ? 0 : d_first"
      :totalRecords="totalRecordsLength"
      :pageLinkSize="pageLinkSize"
      :template="paginatorTemplate"
      :rowsPerPageOptions="rowsPerPageOptions"
      :currentPageReportTemplate="currentPageReportTemplate"
      class="p-paginator-bottom"
      @page="onPage($event)"
      :alwaysShow="alwaysShowPaginator">
      <template #start v-if="$scopedSlots.paginatorstart">
        <slot name="paginatorstart"></slot>
      </template>
      <template #end v-if="$scopedSlots.paginatorend">
        <slot name="paginatorend"></slot>
      </template>
    </DTPaginator>
    <div class="p-datatable-footer" v-if="$scopedSlots.footer">
      <slot name="footer"></slot>
    </div>
    <div ref="resizeHelper" class="p-column-resizer-helper" style="display: none"></div>
    <span
      ref="reorderIndicatorUp"
      class="pi pi-arrow-down p-datatable-reorder-indicator-up"
      style="position: absolute; display: none"
      v-if="reorderableColumns" />
    <span
      ref="reorderIndicatorDown"
      class="pi pi-arrow-up p-datatable-reorder-indicator-down"
      style="position: absolute; display: none"
      v-if="reorderableColumns" />
  </div>
</template>

<script>
import { ObjectUtils, DomHandler, UniqueComponentId } from 'primevue2/utils'
import { FilterMatchMode, FilterOperator, FilterService} from 'primevue2/api'
import Paginator from 'primevue2/paginator'
import TableHeader from './TableHeader.vue'
import TableBody from './TableBody.vue'
import TableFooter from './TableFooter.vue'

export default {
  name: 'DataTable',
  props: {
    value: {
      type: Array,
      default: null
    },
    dataKey: {
      type: String,
      default: null
    },
    rows: {
      type: Number,
      default: 0
    },
    first: {
      type: Number,
      default: 0
    },
    totalRecords: {
      type: Number,
      default: 0
    },
    paginator: {
      type: Boolean,
      default: false
    },
    paginatorPosition: {
      type: String,
      default: 'bottom'
    },
    alwaysShowPaginator: {
      type: Boolean,
      default: true
    },
    paginatorTemplate: {
      type: String,
      default: 'FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink RowsPerPageDropdown'
    },
    pageLinkSize: {
      type: Number,
      default: 5
    },
    rowsPerPageOptions: {
      type: Array,
      default: null
    },
    currentPageReportTemplate: {
      type: String,
      default: '({currentPage} of {totalPages})'
    },
    lazy: {
      type: Boolean,
      default: false
    },
    loading: {
      type: Boolean,
      default: false
    },
    loadingIcon: {
      type: String,
      default: 'pi pi-spinner'
    },
    sortField: {
      type: [String, Function],
      default: null
    },
    sortOrder: {
      type: Number,
      default: null
    },
    defaultSortOrder: {
      type: Number,
      default: 1
    },
    multiSortMeta: {
      type: Array,
      default: null
    },
    sortMode: {
      type: String,
      default: 'single'
    },
    removableSort: {
      type: Boolean,
      default: false
    },
    filters: {
      type: Object,
      default: null
    },
    filterDisplay: {
      type: String,
      default: null
    },
    globalFilterFields: {
      type: Array,
      default: null
    },
    filterLocale: {
      type: String,
      default: undefined
    },
    selection: {
      type: [Array, Object],
      default: null
    },
    selectionMode: {
      type: String,
      default: null
    },
    compareSelectionBy: {
      type: String,
      default: 'deepEquals'
    },
    metaKeySelection: {
      type: Boolean,
      default: true
    },
    contextMenu: {
      type: Boolean,
      default: false
    },
    contextMenuSelection: {
      type: Object,
      default: null
    },
    selectAll: {
      type: Boolean,
      default: null
    },
    rowHover: {
      type: Boolean,
      default: false
    },
    csvSeparator: {
      type: String,
      default: ','
    },
    exportFilename: {
      type: String,
      default: 'download'
    },
    exportFunction: {
      type: Function,
      default: null
    },
    autoLayout: {
      type: Boolean,
      default: false
    },
    resizableColumns: {
      type: Boolean,
      default: false
    },
    columnResizeMode: {
      type: String,
      default: 'fit'
    },
    reorderableColumns: {
      type: Boolean,
      default: false
    },
    expandedRows: {
      type: Array,
      default: null
    },
    expandedRowIcon: {
      type: String,
      default: 'pi-chevron-down'
    },
    collapsedRowIcon: {
      type: String,
      default: 'pi-chevron-right'
    },
    rowGroupMode: {
      type: String,
      default: null
    },
    groupRowsBy: {
      type: [Array, String],
      default: null
    },
    expandableRowGroups: {
      type: Boolean,
      default: false
    },
    expandedRowGroups: {
      type: Array,
      default: null
    },
    stateStorage: {
      type: String,
      default: 'session'
    },
    stateKey: {
      type: String,
      default: null
    },
    editMode: {
      type: String,
      default: null
    },
    editingRows: {
      type: Array,
      default: null
    },
    rowClass: {
      type: null,
      default: null
    },
    rowStyle: {
      type: null,
      default: null
    },
    scrollable: {
      type: Boolean,
      default: false
    },
    scrollDirection: {
      type: String,
      default: 'vertical'
    },
    scrollHeight: {
      type: String,
      default: null
    },
    frozenValue: {
      type: Array,
      default: null
    },
    responsiveLayout: {
      type: String,
      default: 'stack'
    },
    breakpoint: {
      type: String,
      default: '960px'
    },
    showGridlines: {
      type: Boolean,
      default: false
    },
    stripedRows: {
      type: Boolean,
      default: false
    },
    tableStyle: {
      type: null,
      default: null
    },
    tableClass: {
      type: String,
      default: null
    }
  },
  data() {
    return {
      allChildren: null,
      d_first: this.first,
      d_rows: this.rows,
      d_sortField: this.sortField,
      d_sortOrder: this.sortOrder,
      d_multiSortMeta: this.multiSortMeta ? [...this.multiSortMeta] : [],
      d_groupRowsSortMeta: null,
      d_selectionKeys: null,
      d_expandedRowKeys: null,
      d_columnOrder: null,
      d_editingRowKeys: null,
      d_editingMeta: {},
      d_filters: this.cloneFilters(this.filters)
    }
  },
  rowTouched: false,
  anchorRowIndex: null,
  rangeRowIndex: null,
  documentColumnResizeListener: null,
  documentColumnResizeEndListener: null,
  lastResizeHelperX: null,
  resizeColumnElement: null,
  columnResizing: false,
  colReorderIconWidth: null,
  colReorderIconHeight: null,
  draggedColumn: null,
  draggedRowIndex: null,
  droppedRowIndex: null,
  rowDragging: null,
  columnWidthsState: null,
  tableWidthState: null,
  columnWidthsRestored: false,
  watch: {
    first(newValue) {
      this.d_first = newValue
    },
    rows(newValue) {
      this.d_rows = newValue
    },
    sortField(newValue) {
      this.d_sortField = newValue
    },
    sortOrder(newValue) {
      this.d_sortOrder = newValue
    },
    multiSortMeta(newValue) {
      this.d_multiSortMeta = newValue
    },
    selection: {
      immediate: true,
      handler(newValue) {
        if (this.dataKey) {
          this.updateSelectionKeys(newValue)
        }
      }
    },
    expandedRows(newValue) {
      if (this.dataKey) {
        this.updateExpandedRowKeys(newValue)
      }
    },
    editingRows(newValue) {
      if (this.dataKey) {
        this.updateEditingRowKeys(newValue)
      }
    },
    filters: {
      deep: true,
      handler: function (newValue) {
        this.d_filters = this.cloneFilters(newValue)
      }
    }
  },
  beforeMount() {
    if (this.isStateful()) {
      this.restoreState()
    }
  },
  mounted() {
    this.allChildren = this.$children

    this.$el.setAttribute(this.attributeSelector, '')

    if (this.responsiveLayout === 'stack' && !this.scrollable) {
      this.createResponsiveStyle()
    }

    if (this.isStateful() && this.resizableColumns) {
      this.restoreColumnWidths()
    }

    if (this.editMode === 'row' && this.dataKey && !this.d_editingRowKeys) {
      this.updateEditingRowKeys(this.editingRows)
    }
  },
  beforeDestroy() {
    this.unbindColumnResizeEvents()
    this.destroyStyleElement()
    this.destroyResponsiveStyle()
  },
  updated() {
    if (this.isStateful()) {
      this.saveState()
    }

    if (this.editMode === 'row' && this.dataKey && !this.d_editingRowKeys) {
      this.updateEditingRowKeys(this.editingRows)
    }
  },
  methods: {
    columnProp(col, prop) {
      return ObjectUtils.getVNodeProp(col, prop)
    },
    onPage(event) {
      this.clearEditingMetaData()

      this.d_first = event.first
      this.d_rows = event.rows

      let pageEvent = this.createLazyLoadEvent(event)
      pageEvent.pageCount = event.pageCount
      pageEvent.page = event.page

      this.$emit('update:first', this.d_first)
      this.$emit('update:rows', this.d_rows)
      this.$emit('page', pageEvent)
      this.$emit('value-change', this.processedData)
    },
    onColumnHeaderClick(e) {
      const event = e.originalEvent
      const column = e.column

      if (this.columnProp(column, 'sortable')) {
        const targetNode = event.target
        const columnField = this.columnProp(column, 'sortField') || this.columnProp(column, 'field')

        if (DomHandler.hasClass(targetNode, 'p-sortable-column') || DomHandler.hasClass(targetNode, 'p-column-title')
          || DomHandler.hasClass(targetNode, 'p-sortable-column-icon') || DomHandler.hasClass(targetNode.parentElement, 'p-sortable-column-icon')) {
          DomHandler.clearSelection()

          if (this.sortMode === 'single') {
            if (this.d_sortField === columnField) {
              if (this.removableSort && (this.d_sortOrder * -1 === this.defaultSortOrder)) {
                this.d_sortOrder = null
                this.d_sortField = null
              }
              else {
                this.d_sortOrder = this.d_sortOrder * -1
              }
            }
            else {
              this.d_sortOrder = this.defaultSortOrder
              this.d_sortField = columnField
            }

            this.$emit('update:sortField', this.d_sortField)
            this.$emit('update:sortOrder', this.d_sortOrder)
            this.resetPage()
          }
          else if (this.sortMode === 'multiple') {
            let metaKey = event.metaKey || event.ctrlKey
            if (!metaKey) {
              this.d_multiSortMeta = this.d_multiSortMeta.filter(meta => meta.field === columnField)
            }

            this.addMultiSortField(columnField)
            this.$emit('update:multiSortMeta', this.d_multiSortMeta)
          }

          this.$emit('sort', this.createLazyLoadEvent(event))
          this.$emit('value-change', this.processedData)
        }
      }
    },
    sortSingle(value) {
      this.clearEditingMetaData()

      if (this.groupRowsBy && this.groupRowsBy === this.sortField) {
        this.d_multiSortMeta = [
          { field: this.sortField, order: this.sortOrder || this.defaultSortOrder },
          { field: this.d_sortField, order: this.d_sortOrder }
        ]

        return this.sortMultiple(value)
      }

      let data = [...value]

      data.sort((data1, data2) => {
        let value1 = ObjectUtils.resolveFieldData(data1, this.d_sortField)
        let value2 = ObjectUtils.resolveFieldData(data2, this.d_sortField)

        let result = null

        if (value1 == null && value2 != null)
          result = -1
        else if (value1 != null && value2 == null)
          result = 1
        else if (value1 == null && value2 == null)
          result = 0
        else if (typeof value1 === 'string' && typeof value2 === 'string')
          result = value1.localeCompare(value2, undefined, { numeric: true })
        else
          result = (value1 < value2) ? -1 : (value1 > value2) ? 1 : 0

        return (this.d_sortOrder * result)
      })

      return data
    },
    sortMultiple(value) {
      this.clearEditingMetaData()

      if (this.groupRowsBy && (this.d_groupRowsSortMeta || (this.d_multiSortMeta.length && this.groupRowsBy === this.d_multiSortMeta[0].field))) {
        const firstSortMeta = this.d_multiSortMeta[0]
        !this.d_groupRowsSortMeta && (this.d_groupRowsSortMeta = firstSortMeta)

        if (firstSortMeta.field !== this.d_groupRowsSortMeta.field) {
          this.d_multiSortMeta = [this.d_groupRowsSortMeta, ...this.d_multiSortMeta]
        }
      }

      let data = [...value]

      data.sort((data1, data2) => {
        return this.multisortField(data1, data2, 0)
      })

      return data
    },
    multisortField(data1, data2, index) {
      const value1 = ObjectUtils.resolveFieldData(data1, this.d_multiSortMeta[index].field)
      const value2 = ObjectUtils.resolveFieldData(data2, this.d_multiSortMeta[index].field)
      let result = null

      if (typeof value1 === 'string' || value1 instanceof String) {
        if (value1.localeCompare && (value1 !== value2)) {
          return (this.d_multiSortMeta[index].order * value1.localeCompare(value2, undefined, { numeric: true }))
        }
      }
      else {
        result = (value1 < value2) ? -1 : 1
      }

      if (value1 === value2) {
        return (this.d_multiSortMeta.length - 1) > (index) ? (this.multisortField(data1, data2, index + 1)) : 0
      }

      return (this.d_multiSortMeta[index].order * result)
    },
    addMultiSortField(field) {
      let index = this.d_multiSortMeta.findIndex(meta => meta.field === field)

      if (index >= 0) {
        if (this.removableSort && (this.d_multiSortMeta[index].order * -1 === this.defaultSortOrder))
          this.d_multiSortMeta.splice(index, 1)
        else
          this.d_multiSortMeta[index] = { field: field, order: this.d_multiSortMeta[index].order * -1 }
      }
      else {
        this.d_multiSortMeta.push({ field: field, order: this.defaultSortOrder })
      }

      this.d_multiSortMeta = [...this.d_multiSortMeta]
    },
    filter(data) {
      if (!data) {
        return
      }

      this.clearEditingMetaData()

      let globalFilterFieldsArray
      if (this.filters['global']) {
        globalFilterFieldsArray = this.globalFilterFields || this.columns.map(col => this.columnProp(col, 'filterField') || this.columnProp(col, 'field'))
      }

      let filteredValue = []

      for (let i = 0; i < data.length; i++) {
        let localMatch = true
        let globalMatch = false
        let localFiltered = false

        for (let prop in this.filters) {
          if (Object.prototype.hasOwnProperty.call(this.filters, prop) && prop !== 'global') {
            localFiltered = true
            let filterField = prop
            let filterMeta = this.filters[filterField]

            if (filterMeta.operator) {
              for (let filterConstraint of filterMeta.constraints) {
                localMatch = this.executeLocalFilter(filterField, data[i], filterConstraint)

                if ((filterMeta.operator === FilterOperator.OR && localMatch) || (filterMeta.operator === FilterOperator.AND && !localMatch)) {
                  break
                }
              }
            }
            else {
              localMatch = this.executeLocalFilter(filterField, data[i], filterMeta)
            }

            if (!localMatch) {
              break
            }
          }
        }

        if (this.filters['global'] && !globalMatch && globalFilterFieldsArray) {
          for (let j = 0; j < globalFilterFieldsArray.length; j++) {
            let globalFilterField = globalFilterFieldsArray[j]
            globalMatch = FilterService.filters[this.filters['global'].matchMode || FilterMatchMode.CONTAINS](ObjectUtils.resolveFieldData(data[i], globalFilterField), this.filters['global'].value, this.filterLocale)

            if (globalMatch) {
              break
            }
          }
        }

        let matches
        if (this.filters['global']) {
          matches = localFiltered ? (localFiltered && localMatch && globalMatch) : globalMatch
        }
        else {
          matches = localFiltered && localMatch
        }

        if (matches) {
          filteredValue.push(data[i])
        }
      }

      if (filteredValue.length === this.value.length) {
        filteredValue = data
      }

      let filterEvent = this.createLazyLoadEvent()
      filterEvent.filteredValue = filteredValue
      this.$emit('filter', filterEvent)
      this.$emit('value-change', filteredValue)

      return filteredValue
    },
    executeLocalFilter(field, rowData, filterMeta) {
      let filterValue = filterMeta.value
      let filterMatchMode = filterMeta.matchMode || FilterMatchMode.STARTS_WITH
      let dataFieldValue = ObjectUtils.resolveFieldData(rowData, field)
      let filterConstraint = FilterService.filters[filterMatchMode]

      return filterConstraint(dataFieldValue, filterValue, this.filterLocale)
    },
    onRowClick(e) {
      const event = e.originalEvent
      if (DomHandler.isClickable(event.target)) {
        return
      }

      this.$emit('row-click', e)

      if (this.selectionMode) {
        const rowData = e.data
        const rowIndex = this.d_first + e.index

        if (this.isMultipleSelectionMode() && event.shiftKey && this.anchorRowIndex != null) {
          DomHandler.clearSelection()
          this.rangeRowIndex = rowIndex
          this.selectRange(event)
        }
        else {
          const selected = this.isSelected(rowData)
          const metaSelection = this.rowTouched ? false : this.metaKeySelection
