<template>
  <div
    :class="['p-checkbox p-component', { 'p-checkbox-focused': focused, 'p-disabled': $attrs.disabled }]"
    @click="onClick"
    @keydown.space.prevent="onClick">
    <div
      ref="box"
      :class="[
        'p-checkbox-box p-component',
        { 'p-highlight': checked, 'p-disabled': $attrs.disabled, 'p-focus': focused },
      ]"
      role="checkbox"
      :aria-checked="checked"
      :tabindex="$attrs.disabled ? null : '0'"
      @focus="onFocus($event)"
      @blur="onBlur($event)">
      <span :class="['p-checkbox-icon', { 'pi pi-check': checked }]"></span>
    </div>
  </div>
</template>

<script>
export default {
  inheritAttrs: false,
  props: {
    checked: null
  },
  data() {
    return {
      focused: false
    }
  },
  methods: {
    onClick(event) {
      if (!this.$attrs.disabled) {
        this.focused = true
        this.$emit('change', {
          originalEvent: event,
          checked: !this.checked
        })
      }
    },
    onFocus() {
      this.focused = true
    },
    onBlur() {
      this.focused = false
    }
  }
}
</script>
